* {
    transition: all 0.2s ease-in-out;
}

body {
    overflow: hidden;
    background-color: #AF86F5;
    width: 100vw;
    height: 100vh;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}

.form {
    margin: auto;
    margin-top: 5px;
    align-items: center;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.form * {
    margin-top: 15px;
    margin-bottom: 15px;
}

.form a {
    margin-top: 0px;
    margin-bottom: 0px;
}

h1 {
    font-weight: 800;
}

#app {
    background-image: linear-gradient(135deg, #2C059E, #FF00B7);
    width: 100vw;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: auto;
    overflow: hidden;
}

.container {
    width: 95%;
    min-width: 95%;
    height: 95%;
    background-color: #ffffff;
    transform: translate(0%, 2.5%);
    border-radius: 10px;
}

.items-center {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column-reverse;
}

.v-center {
    vertical-align: middle;
    height: 100vh;
    width: 100vw;
}

.form-control {
    transition: all 0.3s ease-in-out;
    border: 2px solid var(--bs-border-color);
    border-radius: 7px;
    width: 100%;
    max-width: 300px;
}

@media only screen and (max-width: 375px) {
    .form-control {
        width: 100%;
        max-width: none;
    }

    form {
        width: 90%;
    }
}

.modal {
    display: block;
    position: relative;
    height: fit-content;
}

.algin-items-center {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center
}

.d-flex-row {
    display: flex;
    flex-direction: row;
}
.d-flex-col {
    display: flex;
    flex-direction: column;
}

.top-100 {
    transform: translateY(-100%);
}

.point {
    cursor: pointer;
}

.pad div {
    padding: 0;
    border-radius: 10px;
}

.container {
    height: 95vh;
    background-color: #ffffff;
}
.margin *{
    margin-top: 5px;
    margin-bottom: 5px;
}